<script>
import { mapGetters, mapState, mapActions, mapMutations } from 'vuex'
import axios from 'axios'
import { API_BASE_URL } from '../../config'
import productEnding from '@/helpers/productEnding'
import BaseFormText from '@/components/BaseFormText.vue'
import BaseFormTextarea from '@/components/BaseFormTextarea.vue'
import OrderList from '@/components/OrderList.vue'
import FieldMessageOrder from '@/components/FieldMessageOrder.vue'
import BaseLoader from '@/components/BaseLoader.vue'
import FailedLoader from '@/components/FailedLoader.vue'
export default {
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: '',
      orderLoader: false,
      orderLoaderFailed: false,
    }
  },
  components: {
    BaseLoader,
    FailedLoader,
    BaseFormText,
    BaseFormTextarea,
    OrderList,
    FieldMessageOrder,
  },
  methods: {
    ...mapMutations(['updateOrderInfo', 'resetCart']),
    sendOrder() {
      this.$store.commit('updateProductsCountForAPI', this.productsCount)
      let prodCount = this.$store.state.productsCountForAPI
      console.log('orderPage sendOrder ProdCount ' + prodCount);
      this.orderLoader = true
      this.orderLoaderFailed = false
      return (new Promise(resolve => {
        setTimeout((resolve) => {
          this.formError = {}
          this.formErrorMessage = ''

          axios
            .post(API_BASE_URL + '/api/orders',
              { ...this.formData },
              {
                params: {
                  userAccessKey: this.$store.state.userAccessKey
                }
              })
            .then(res => {
              this.orderLoader = false

              this.formData = {}
              this.formError = {}

              this.resetCart()
              this.updateOrderInfo({
                orderInfo: res.data,
                productsCount: prodCount,
              })
              this.$router.push({
                name: 'orderinfo', params: { id: res.data.id }
              })
            })
            .catch(error => {
              this.orderLoaderFailed = true
              this.orderLoader = false
              console.log(error);
              this.formError = error.response.data.error.request || {}
              this.formErrorMessage = error.response.data.error.message
            })
        }, 2000)
      }))

    }
  },
  computed: {
    ...mapGetters({
      products: 'cartInfoProducts',
      totalPrice: 'cartTotalPrice',
      productsCount: 'productsCount',
    }),

    ...mapState(['cartProducts', 'cartProductsData', 'cartLoader', 'orderInfo']),
    // ...mapActions(['loadOrder']),
    productEnd(count) {
      return productEnding
    },
  },

}
</script>

