        <!-- <OrderList></OrderList> -->
        <OrderList :products="products" :total-price="totalPrice" :products-count="productsCount">
          <button v-show="!cartLoader
            // && productsCount > 0
            " :disabled="productsCount == 0" class="cart__button button button--primery" type="submit"
            key="order-send-button">
            Оформить заказ
          </button>
        </OrderList>
        <transition name="fade">
          <FieldMessageOrder :error="formErrorMessage" v-show="formErrorMessage" />
        </transition>
      </form>
    </section>
  </main>
</div></template>

<style>
.fade-enter-active {
  transition: all .3s ease;
}

.fade-leave-active {
  transition: all .5s ease;
}

.fade-enter,
.fade-leave-to {
  transform: translateY(20px);
  opacity: 0;
}
</style>