    randomProduct(){
      let max=this.productsLength
      let randomNumber = Math.ceil(Math.random() * max)
      this.$router.push({name: 'product', params: { id: randomNumber }})
    },
  },
  computed:{
    products() {
      return products
    },
    productsLength() {
      return products.length
    }
  },
}
</script>

