<script>
export default {
  props: {
    name: String,
    color: {
      type: Object,
      required: true,
    },
    selectedColor: Number,
  },
  data() {
    return {
      colorCode: null,
    }
  },
  methods: {
    changeColor() {
      this.$emit('update:selectedColor', this.colorCode)
    }
  },
  computed: {

  },
}
</script>

<template>
  <li class="colors__item">
    <label class="colors__label">
      <input class="colors__radio sr-only" type="radio" :name="name" @change="changeColor" :value="color.id"
        v-model="colorCode">
      <span class="colors__value" :style="{ 'background-color': this.color.code }">
      </span>
    </label>
  </li>
</template>